import React from 'react';
import {Image, Text, TouchableOpacity, View} from 'react-native';
import {useNavigation} from '@react-navigation/native';
import {AppNativeStackNavigation} from '@/navigations';
import {Header, Screen, ScrollView} from '@/components';
import useFetchTodoList from './apis/useFetchTodoList';

export type ClaimListProps = undefined;

const ClaimListScreen = () => {
  const {data} = useFetchTodoList();
  const navigation = useNavigation<AppNativeStackNavigation>();

  return (
    <>
      <Header dark title="Claim list" />
      <Screen style={{paddingLeft: 16, paddingRight: 16}}>
        <ScrollView>
          <View>
            <Image
              source={require('@/assets/summer.jpg')}
              style={{height: 100, width: '100%', aspectRatio: 1}}
              resizeMode="contain"
            />
            {data?.map(item => (
              <TouchableOpacity
                onPress={() => {
                  navigation.navigate('claimApp', {
                    screen: 'claimDetail',
                    params: {id: item.id},
                  });
                }}
                key={item.id}
                style={{
                  backgroundColor: '#d3d3d3',
                  padding: 16,
                  marginBottom: 16,
                }}>
                <Text>{item.title}</Text>
              </TouchableOpacity>
            ))}
          </View>
        </ScrollView>
      </Screen>
    </>
  );
};

export default ClaimListScreen;
